
body {
  margin: 0;
  padding: 0;
}
ul, li {
  margin: 0;
  padding: 0;
  list-style: none;
}
p {
  margin: 0 0 10px 0;
}
a {
  text-decoration: none;
  color: #333;
}
a:hover {
    color: #888;
}
@font-face {
  font-family: 'myBlog';
  src: url("assets/font/blog.eot");
  /* IE9 Compat Modes */
  src: url("assets/font/blog.eot") format("embedded-opentype"), url("assets/font/blog.woff") format("woff"), url("assets/font/blog.ttf") format("truetype"), url("assets/font/blog.svg") format("svg");
  /* Legacy iOS */
}
.blog-icon {
  font-family: 'myBlog';
}
.icon-delete:before {
  content: 'd';
}
.icon-modify:before {
  content: 'e';
}
.my-btn {
  display: block;
  width: 100%;
  height: 42px;
  padding: 8px 12px;
  line-height: 1.71429;
  color: #5c5c5c;
  background-color: #fff;
  background-image: none;
  border: 1px solid #e6e6e6;
  border-radius: 5px;
  outline: none;
}
.my-btn:hover, .my-btn:focus {
    color: #a0a0a0;
    background-color: #fff;
    border-color: #ebebeb;
}
.my-input {
  display: block;
  width: 100%;
  height: 42px;
  padding: 8px 12px;
  line-height: 1.71429;
  color: #5c5c5c;
  background-color: #fff;
  background-image: none;
  border: 1px solid #e6e6e6;
  border-radius: 5px;
  outline: none;
}
* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
#app {
  position: relative;
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  color: #2c3e50;
  font-size: 14px;
  line-height: 24px;
  height: 100%;
  min-width: 320px;
}
#app .my-cover {
    position: relative;
    padding: 0 15px;
    margin: auto;
}
@media (min-width: 768px) {
#app .my-cover {
        width: 750px;
}
}
#app header {
    position: absolute;
    height: 60px;
    width: 100%;
    top: 0;
    left: 0;
    -webkit-transition: height .5s;
    transition: height .5s;
    overflow: hidden;
    background-color: #fff;
}
#app header.on-open {
      height: 200px;
      border-bottom: none;
}
#app header.on-open .nav-bar .icon-bar:first-child {
        -webkit-transform: rotate(45deg) translateY(8px);
                transform: rotate(45deg) translateY(8px);
}
#app header.on-open .nav-bar .icon-bar:nth-child(2) {
        opacity: 0;
}
#app header.on-open .nav-bar .icon-bar:nth-child(3) {
        -webkit-transform: rotate(-45deg) translateY(-8px);
                transform: rotate(-45deg) translateY(-8px);
}
#app header .fix-nav {
      position: relative;
      height: 60px;
      border-bottom: 1px #e0d6c1 solid;
      z-index: 10;
}
#app header .fix-nav .my-cover {
        height: 100%;
}
#app header .logo-name {
      position: absolute;
      top: 0;
      left: 15px;
      display: inline-block;
      font-size: 20px;
      padding: 15px 0;
      font-weight: 500;
      line-height: 30px;
}
#app header .menu-nav {
      position: absolute;
      padding-top: 60px;
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
      z-index: 9;
}
@media (min-width: 768px) {
#app header .menu-nav {
          position: absolute;
          display: block;
          width: auto;
          height: auto;
          left: 50%;
          margin-left: 245px;
          padding: 0;
          z-index: 10;
}
}
#app header .menu-nav .menu-list {
        display: block;
}
@media (min-width: 768px) {
#app header .menu-nav .menu-list {
            display: table;
}
}
#app header .menu-nav .menu-list li {
          display: block;
          background-color: #f9f9f9;
}
@media (min-width: 768px) {
#app header .menu-nav .menu-list li {
              display: table-cell;
              background-color: #fff;
}
}
#app header .menu-nav .menu-list li a {
            display: block;
            line-height: 24px;
            padding: 5px 15px;
}
@media (min-width: 768px) {
#app header .menu-nav .menu-list li a {
                display: inline-block;
                padding: 17.5px 15px;
}
}
#app header .nav-bar {
      position: absolute;
      right: 15px;
      padding: 9px 10px;
      margin: 14px 8px;
      cursor: pointer;
}
@media (min-width: 768px) {
#app header .nav-bar {
          display: none;
}
}
#app header .nav-bar .icon-bar {
        display: block;
        width: 22px;
        height: 2px;
        border-radius: 1px;
        background-color: #888;
        -webkit-transition: all 0.5s;
        transition: all 0.5s;
}
#app header .nav-bar .icon-bar + .icon-bar {
          margin-top: 4px;
}
#app #main-body {
    position: relative;
    padding: 60px 0;
    min-height: 100vh;
}
#app #main-body ::-webkit-scrollbar {
      width: 2px;
      /* for vertical scrollbars */
      height: 2px;
      /* for horizontal scrollbars */
}
#app #main-body ::-webkit-scrollbar-track {
      background: transparent;
}
#app #main-body ::-webkit-scrollbar-thumb {
      background: rgba(0, 0, 0, 0.5);
}
#app #main-body .login-area, #app #main-body .sign-area {
      position: relative;
      max-width: 400px;
      padding: 15px;
      margin: 80px auto 40px;
}
@media (max-width: 480px) {
#app #main-body .login-area, #app #main-body .sign-area {
          margin: 40px auto 20px;
}
}
#app #main-body .login-area .fill-area, #app #main-body .sign-area .fill-area {
        width: 100%;
        margin-bottom: 20px;
}
#app footer {
    position: absolute;
    width: 100%;
    bottom: 0;
    background-color: #484848;
    color: #8a8a8a;
}
#app footer .footer-text {
      text-align: center;
      padding: 20px 15px;
}



